
import { defineComponent } from "vue";
import ErrorMessage from "@/components/ErrorMessage.vue";

import ValidateError from "@/components/ValidateError.vue";
import { CompanyService } from "@/services/CompanyService";
import { AdminerService } from "@/services/AdminerService";

export default defineComponent({
  name: "AdminerStore",
  components: {
    ErrorMessage,
    ValidateError,
  },
  data: () => ({
    isShow: false,
    message: "",
    submitDisable: false,
    validateErrors: {} as any,
    adminer: {} as any,
    items: {
      company_id: null,
    } as any,

    selectCategoryGroupId: null as null | number,
    companies: [] as any[],

    adminerService: new AdminerService(),
    companyService: new CompanyService(),
  }),
  mounted: async function () {
    const campanyResult = await this.companyService.indexApi({
      per_page: -1,
    });
    if (!campanyResult.success) {
      this.commonError(campanyResult);
      return;
    }
    this.companies = campanyResult.data;
    this.isShow = true;
  },
  methods: {
    commonError: function (result: any = null): void {
      if (result.status === 422) {
        this.validateErrors = result.data;
      }
      if (result.message !== "") {
        this.message = result.message;
      }
    },
    save: async function () {
      this.validateErrors = {};
      this.message = "";
      this.submitDisable = true;
      const result = await this.adminerService.storeApi(this.items);
      this.submitDisable = false;
      if (!result.success) {
        this.commonError(result);
        return;
      }
      alert("作成しました");
      this.$router.push({
        name: "AdminerUpdate",
        params: {
          id: result.data.id,
        },
      });
    },
  },
});
